import React, {useEffect, useState} from 'react';
import {
    MenuItem,
    Select,
    InputLabel,
    Box,
    Typography,
    Button,
    Skeleton
} from '@mui/material';
import Grid from '@mui/material/Grid';
import  Flag  from "react-world-flags";
import CircularProgress from "@mui/material/CircularProgress";
import Backdrop from "@mui/material/Backdrop";
import Dialog from "@mui/material/Dialog";
import DialogTitle from "@mui/material/DialogTitle";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogActions from "@mui/material/DialogActions";
import FlagIcon from "@mui/icons-material/Flag";
import {People} from "@mui/icons-material";


const API_ENDPOINT = "api.mocaland.ir:88/QandShekan"


export default function BuyService({user_id , active}) {
    const [loading, setLoading] = useState(true);
    const [PopUpLoading, setPopUpLoading] = useState(false);
    const [PopUpText , setPopUpText] = useState({"status":false});
    function ClosePopUpText(){
        setPopUpText({"status":false});
    }

    const [VPNLocations , setVPNLocations] = useState([]);
    const [country, setCountry] = useState('');
    const [userCount, setUserCount] = useState(1);
    const [Plans , setPlans] = useState([])

    const PlanItem = ({ Plan }) => {
        const { name, amount, id, location, ip_limit} = Plan;

        return (
            <Box
                sx={{
                    mt: 2,
                    mb: 2,
                    p: 2,
                    border: 1,
                    borderColor: "grey.300",
                    width: "95%",
                }}
            >
                <Typography variant="h6" sx={{direction:"rtl"}}>{name}</Typography>

                <Box display="flex" alignItems="center" sx={{ mt: 1 ,direction:"rtl"}}>
                    <Grid container spacing={1}>
                        <Grid item xs={7}>
                            <FlagIcon sx={{"height":"20px",mb:-0.5}}/>
                            <Typography variant="p" color="text.secondary" sx={{ ml: 1 ,"font-size":"14px"}}>{`کشور: ${location}`}</Typography>
                            <Flag code={location} height="16" />
                        </Grid>
                        <Grid item xs={5}>
                            <People sx={{"height":"20px",mb:-0.5}}/>
                            <Typography variant="p" color="text.secondary" sx={{ ml: 1 ,"font-size":"14px"}}>{`کاربران مجاز: ${ip_limit}`}</Typography>
                        </Grid>
                    </Grid>
                </Box>

                    <Box display="flex" justifyContent="space-between" sx={{ mt: 2 ,direction:"rtl"}}>
                        <Grid container spacing={1}>
                            <Grid item xs={12}>
                                <Button onClick={()=>ProccessBuyPlan(id)}
                                        variant="contained" color="primary" sx={{width:"95%"}}>خرید با قیمت {amount} تومان</Button>
                            </Grid>
                        </Grid>
                    </Box>


            </Box>
        );
    };
    const handleCountryChange = (event) => {
        setCountry(event.target.value);
    };
    const handleUserCountChange = (event) => {
        setUserCount(event.target.value);
    };
    function GetLocations(){
        setPopUpLoading(true);
        if (active) {
            var formdata = new FormData();
            formdata.append("action", "GetLocations");
            var requestOptions = {
                method: 'POST',
                body: formdata,
                redirect: 'follow'
            };

            fetch(`https://${API_ENDPOINT}/api.php`, requestOptions)
                .then(response => response.text())
                .then(result => {
                    setPopUpLoading(false);
                    const res = JSON.parse(result);
                    if (res.status) {
                        setVPNLocations(res.locations);
                        setCountry(res.locations[0].code);
                    }else{
                        setPopUpText({"status":true,"title":"خطا","text":res.msg});
                    }
                })
                .catch(error => {
                    setPopUpText({"status":true,"title":"خطای ناشناخته","text":"لطفا مجددا تلاش کنید!"});
                    setPopUpLoading(false);
                });
        }
    }
    function GetPlans(){
        setLoading(true);
        if (active && country !== null && country !== "") {
            var formdata = new FormData();
            formdata.append("action", "GetPlansList");
            formdata.append("location", country);
            formdata.append("ip_limit", userCount);
            var requestOptions = {
                method: 'POST',
                body: formdata,
                redirect: 'follow'
            };

            fetch(`https://${API_ENDPOINT}/api.php`, requestOptions)
                .then(response => response.text())
                .then(result => {
                    setLoading(false);
                    const res = JSON.parse(result);
                    if (res.status) {
                        setPlans(res.plans);
                    }else{
                        setPopUpText({"status":true,"title":"خطا","text":res.msg});
                    }
                })
                .catch(error => {
                    setLoading(false);
                    setPopUpText({"status":true,"title":"خطای ناشناخته","text":"لطفا مجددا تلاش کنید!"});
                });
        }
    }
    function ProccessBuyPlan(PlanId){
        setPopUpLoading(true);
        var formdata = new FormData();
        formdata.append("action", "Buy_new_config");
        formdata.append("user_id", user_id);
        formdata.append("plan_id", PlanId);
        formdata.append("ip_limit", userCount);
        var requestOptions = {
            method: 'POST',
            body: formdata,
            redirect: 'follow'
        };

        fetch(`https://${API_ENDPOINT}/api.php`, requestOptions)
            .then(response => response.text())
            .then(result => {
                setPopUpLoading(false);
                const res = JSON.parse(result);
                if (res.status) {
                    setPopUpText({"status":true,"title":"موفق","text":res.msg});
                }else{
                    setPopUpText({"status":true,"title":"ناموفق","text":res.msg});
                }
            })
            .catch(error => {
                setPopUpLoading(false);
                setPopUpText({"status":true,"title":"ناموفق","text":"خطای ناشناخته ای رخ داد ، لطفا دقایقی بعد مجددا امتحان کنید!"});
            });
    }
    useEffect(() => {
        GetLocations();
    }, [active]);
    useEffect(() => {
        GetPlans();
    }, [country,userCount]);

    if (active)
        return (
            <>
            {PopUpLoading === false && VPNLocations.length > 0
                    ?
                    <Box sx={{ display: "flex", flexDirection: "column", alignItems: "center" ,fontFamily:"iranyekan"}}>
                        <Box
                            sx={{
                                mt: 2,
                                mb: 2,
                                p: 2,
                                border: 1,
                                borderColor: "grey.300",
                                width: "95%",
                                direction:"rtl"
                            }}
                        >
                            <Typography variant="h8" sx={{direction:"rtl"}}>به کمک این قسمت میتوانید فیلتر دلخواه را اعمال نمایید.</Typography>
                            <Grid container >
                                <Grid item xs={7}>
                                    <InputLabel id="country-select-label" sx={{display:"inline"}}>کشور</InputLabel>
                                    <Select
                                        sx={{mr:"5px" , width:"65%" , height:"35px",mt:"15px"}}
                                        labelId="country-select-label"
                                        id="country-select"
                                        value={country}
                                        label="کشور"
                                        onChange={handleCountryChange}
                                    >
                                        {VPNLocations.map((option) => (
                                            <MenuItem key={option.code} value={option.code}>
                                                <Flag code={option.code} height={15} />
                                                {option.label}
                                            </MenuItem>
                                        ))}
                                    </Select>
                                </Grid>
                                <Grid item xs={5}>
                                    <div>
                                        <InputLabel id="user-count-select-label" sx={{display:"inline"}}>تعداد کاربر</InputLabel>
                                        <Select
                                            sx={{mr:"5px" , width:"45%" , height:"35px",mt:"15px"}}
                                            labelId="user-count-select-label"
                                            id="user-count-select"
                                            value={userCount}
                                            label="تعداد کاربر"
                                            onChange={handleUserCountChange}

                                        >
                                            {[...Array(10).keys()].map((number) => (
                                                <MenuItem key={number} value={number + 1}>
                                                    {number + 1}
                                                </MenuItem>
                                            ))}
                                        </Select>
                                    </div>
                                </Grid>
                            </Grid>
                        </Box>
                        <Backdrop
                            sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
                            open={PopUpLoading}
                        >
                            {/* CircularProgress برای نمایش یک انیمیشن بارگذاری استفاده می‌شود */}
                            <CircularProgress color="inherit" />
                        </Backdrop>
                        <div>
                            <Dialog open={PopUpText.status} sx={{direction:"rtl"}} onClose={()=>ClosePopUpText()}>
                                <DialogTitle>{PopUpText.title}</DialogTitle>
                                <DialogContent>
                                    <DialogContentText>
                                        {PopUpText.text}
                                    </DialogContentText>
                                </DialogContent>
                                <DialogActions>
                                    <Button onClick={()=>ClosePopUpText()}>بستن</Button>
                                </DialogActions>
                            </Dialog>
                        </div>

                            {loading
                                ? // show skeleton while loading
                                Array.from(new Array(2)).map((_, index) => (
                                    <Box
                                        key={index}
                                        sx={{
                                            mt: 2,
                                            mb: 2,
                                            p: 2,
                                            border: 1,
                                            borderColor: "grey.300",
                                            width: "95%",
                                            direction: "rtl"
                                        }}
                                    >
                                        <Skeleton variant="text" width="40%" />
                                        <Skeleton variant="rectangular" height={24} sx={{ mt: 1 }} />
                                        <Skeleton variant="text" width="30%" sx={{ mt: 1 }} />
                                        <Skeleton variant="rectangular" height={16} sx={{ mt: 1 }} />
                                        <Skeleton variant="rectangular" height={24} sx={{ mt: 2 }} />
                                    </Box>
                                ))
                                : // show service items after loading
                                Plans.map((Plan, index) => (
                                    <PlanItem key={index} Plan={Plan} />
                                ))}


                    </Box>

                    :
                    <Box sx={{ display: "flex", flexDirection: "column", alignItems: "center" ,fontFamily:"iranyekan"}}>
                        <Backdrop
                            sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
                            open={PopUpLoading}
                        >
                            {/* CircularProgress برای نمایش یک انیمیشن بارگذاری استفاده می‌شود */}
                            <CircularProgress color="inherit" />
                        </Backdrop>
                        <div>
                            <Dialog open={PopUpText.status} sx={{direction:"rtl"}} onClose={()=>ClosePopUpText()}>
                                <DialogTitle>{PopUpText.title}</DialogTitle>
                                <DialogContent>
                                    <DialogContentText>
                                        {PopUpText.text}
                                    </DialogContentText>
                                </DialogContent>
                                <DialogActions>
                                    <Button onClick={()=>ClosePopUpText()}>بستن</Button>
                                </DialogActions>
                            </Dialog>
                        </div>
                    </Box>
            }
                </>
        );
    return (<></>);
}
